<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view class="grace-form grace-margin-top">
				<view class="grace-items">
					<text class="grace-icons icon-address icon grace-blue"></text>
					<view class="grace-label">私有服务器</view>
					<input type="text" class="input text-align-left" name="privateServer" v-model="privateServer" placeholder="请输入私有服务器地址" />
					<!-- #ifndef H5 -->
					<view class="grace-icons icon-photograph icon grace-blue" @tap="scanPrivate"></view>
					<!-- #endif -->
				</view>
				<view class="grace-margin-top"><button type="primary" class="grace-button" @click="saveServer">保存服务器地址</button></view>
			</view>
		</view>
	</gracePage>
</template>

<script>
export default {
	data() {
		return {
			privateServer: '',
			returnUrl: ''
		};
	},
	onLoad: function(parameters) {
		var me = this;
		var serverList = uni.getStorageSync('serverList');
		me.privateServer = serverList.privateServer;
		var returnUrl = parameters.returnurl;
		if (returnUrl == null || typeof returnUrl == undefined) {
			uni.reLaunch({
				url: 'login'
			});
		} else {
			me.returnUrl = returnUrl;
		}
	},
	methods: {
		saveServer() {
			var me = this;
			var serverList = {
				privateServer: me.privateServer
			};
			uni.setStorageSync('serverList', serverList);
			uni.reLaunch({
				url: me.returnUrl
			});
		},
		scanPrivate() {
			var me = this;
			uni.scanCode({
				//onlyFromCamera: true,
				success: function(res) {
					me.privateServer = res.result;
				}
			});
		},
		onNavigationBarButtonTap(e) {
			if (e.type == 'back') {
				uni.reLaunch({
					url: 'login'
				});
			}
		}
	}
};
</script>

<style></style>
